export default ".menu-toggle {\n  display: none;\n}\n\n.mobile-bar {\n  z-index: 10;\n  position: fixed;\n  top: 0;\n  left: 0;\n  padding: 0 25px;\n  /*BAR HOW MUCH IN RIGHT*/\n  width: 100%;\n  height: 65px;\n  background-color: #194598e6;\n}\n\n.menu-icon {\n  float: right;\n  display: block;\n  position: relative;\n  width: 25px;\n  height: 100%;\n  cursor: pointer;\n  transition: transform 300ms ease-in;\n  /*the bar to X*/\n}\n\n.menu-icon > span {\n  display: block;\n  position: absolute;\n  top: 55%;\n  margin-top: -0.3em;\n  width: 100%;\n  height: 0.199em;\n  border-radius: 1px;\n  background-color: #f50000;\n  transition: transform 300ms ease-in;\n}\n\n.menu-icon > span:before,\n.menu-icon > span:after {\n  content: \"\";\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  border-radius: 1px;\n  background-color: #f50000;\n  transition: transform 300ms ease;\n}\n\n.menu-icon > span:before {\n  transform: translateY(-0.6em);\n}\n\n.menu-icon > span:after {\n  transform: translateY(0.6em);\n}\n\n/*need this for bringing down the examples*/\n\n.menu-toggle:checked + .mobile-bar .menu-icon {\n  transform: rotate(45deg);\n}\n\n.menu-toggle:checked + .mobile-bar span:before,\n.menu-toggle:checked + .mobile-bar span:after {\n  transform: rotate(90deg);\n}\n\n.menu-toggle:checked ~ .header {\n  visibility: visible;\n  opacity: 1;\n  transition-delay: 0s;\n}\n\n.header {\n  visibility: hidden;\n  opacity: 0;\n  position: fixed;\n  top: 0;\n  left: 0;\n  padding-top: 60px;\n  width: 100%;\n  height: 100%;\n  background-color: #194598d9;\n  padding-right: 5.7px;\n  color: #eee;\n  transition: opacity 300ms ease-in, visibility 0s ease-in 300ms;\n}\n\n.header nav,\n.header ul {\n  height: 100%;\n}\n\n.header li {\n  border-bottom: 1px solid #555;\n  font-family: \"Anonymous Pro\", monospace;\n  font-weight: 600;\n  font-size: 18px;\n  color: #fff;\n  font-weight: 430;\n  line-height: 2.4;\n  font-size: 15px;\n  letter-spacing: 0.2px;\n  font-weight: 600;\n}\n\n.header li a:hover {\n  color: #66d9ef;\n  transition: color 300ms ease-in;\n}\n\n.header a {\n  display: block;\n  padding: 25px;\n  color: #e7dfca;\n  font-size: 18px;\n  text-decoration: none;\n  transition: background-color 300ms ease-in;\n}\n\n.navbar-brand {\n  padding: 0px;\n}\n\n.navbar-brand .logo {\n  height: 40px;\n  width: auto;\n  z-index: 10;\n  position: fixed;\n  top: 13.45px;\n  left: 0;\n  padding: 0 25px;\n  color: #facc44;\n  font-size: 30px;\n}\n\n.header-wrap {\n  display: block;\n  margin-bottom: 100px;\n}\n\n.header-main {\n  top: 0;\n  width: 100%;\n  position: fixed;\n}\n\n@media (min-width: 1000px) {\n  .mobile-bar {\n    display: none;\n  }\n\n  .header {\n    z-index: 5;\n    position: relative;\n    left: 0;\n    padding-top: 0;\n    width: 100%;\n    height: auto;\n    overflow: auto;\n    visibility: visible;\n    opacity: 1;\n    text-align: right;\n  }\n\n  .header li {\n    display: inline-block;\n    margin-right: -6px;\n    /* fix the inline-block gap */\n    border: none;\n    text-align: left;\n  }\n\n  .header a {\n    padding: 15px 35px;\n    /*header height and text spacing*/\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"